body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow-y: auto;  /* Aktivera endast vertikal skrollning */
  overflow-x: hidden; /* Förhindra horisontell skrollning */
  position: relative;
  touch-action: pan-y;
  
 
}


/* Container for Map and Cards */
.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
 position: relative;
  
  
}

/* Justera knappens position */
button.MuiButtonBase-root {
  z-index: 20; /* Säkerställer att den visas ovanpå kartan */

 
}

/* Map Container */
.map-container {
  flex: 1;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
 z-index: 2;
  overflow: hidden; /* Förhindra skrollning */
}



.spinner {
  animation: spin 1s infinite linear;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.search-container {
  position: static; /* Ta bort positionering eller sätt till static */
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  width: 100%;
  
  font-size: 16px;
  transition: top 0.3s ease-in-out, margin-top 0.3s ease-in-out;
  display: flex;
  justify-content: center;
  padding: 10px;
  pointer-events: none;
  
}
.button-scroll-container {
  display: flex;
  overflow-x: scroll;
  white-space: nowrap;
  gap: 10px;
  padding: 10px 0;
}

.button-scroll-container::-webkit-scrollbar {
  display: none; /* Göm scrollbar på webkit-baserade webbläsare */
}

.button-scroll-container {
  -ms-overflow-style: none; /* Göm scrollbar för IE och Edge */
  scrollbar-width: none; /* Göm scrollbar för Firefox */
}

.scroll-button {
  flex: 0 0 auto; /* Håll varje knapp till sin naturliga storlek */
  padding: 10px 20px;
  border-radius: 25px;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  box-shadow: 0 2px 4px #dd4122eb;
}

.search-container > * {
  pointer-events: auto; /* Tillåt interaktioner på barnen (som texter och inputfält) */
  
}
  .search-container.center {
    margin-top: 200px;
    font-size: 16px;
    position: absolute; /* Changed from fixed to absolute */
  }

  .search-container.top {
    top: 70px;
  
    font-size: 16px;
    margin-right: 15px;
    position: absolute; /* Changed from fixed to absolute */
    
  }
  .search-container.no {
    z-index: -1
    
  }
.loading-spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
}
.initial-text {
  position: relative;
  margin-top: 10px; /* Justera efter behov */
  left: 50%;
  transform: translateX(-50%);
  z-index: 133;
  
  color: #d6b2e2;
 
  padding: 5px 20px;
  width: 100%; /* Gör elementet fullbredd */
  max-width: 1000px; /* Maximal bredd för större skärmar */
  height: 140px; /* Justera höjden */
  background: url('/public/förskolekollen.png') no-repeat center center;
  
 
  
  text-align: center; /* Centrera texten */
}
.header-container {
  height: 1px;

  z-index: 1; /* Se till att headern ligger över andra element */

  
}
